/* ======= HEADER ======= */
header {
  background-color: #2D4A22;
  padding: 2rem 0 10rem;
  text-align: center;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0px;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border-width: 0px;
}

header h1 {
  color: #fff;
  font-weight: lighter;
}

.container {
  width: min(90vw, 800px);
  margin: auto;
}